import { Box, Divider, Paper, Typography } from '@material-ui/core';
import React from 'react';
import TopNavigationRefer from '..';
import { makeStyles } from '@material-ui/styles';

const usePaperStyles = makeStyles({
  root: {
    width: '20rem',
    padding: '2rem',
  }
})


const ReferPopover = React.memo(function ReferPopover(props: any){
  const paperClasses = usePaperStyles();
  return <Paper classes={paperClasses}>{props.message || <Typography variant="body1">You have the <strong>maximum</strong> number of activities in your bookmarks. To allow for more activities, click the button below</Typography>}<Box m="1rem 1rem"><Divider /></Box><TopNavigationRefer /></Paper>
})

export default ReferPopover;